<template>
  <div id="_wrapper">
    <div style=“margin-top:30%”>
      <canvas id="process" width="250" height="250"></canvas>
      <div class="drawcanvas">
        <video  id="_webcam" playsinline class=""></video>
        <canvas id="_imageData"></canvas>
        <canvas id="_faceSub"></canvas>
        <canvas id="_t3d"></canvas>
        <canvas id="_f3d"></canvas>
        <canvas id="_drawing"></canvas>
        <div id="_stats"></div>
      </div>
      <!-- <div id="_progressBar"></div> -->
    </div>
    <p class="wz-tip" v-html="wztip"></p>
    <p class="time-return" v-if="eventtype === 'init'">{{downtime}}</p>
  </div>
</template>

<script>
var color = '#6ABF8B'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      width: 250,
      height: 250,
      downtime: 10,
      wztip: '请正视手机<br>保持禁止不动',
      eventtype: 'init'
    }
  },
  mounted () {
    this.drawCanvas()
    window.brfv4Example.start()
    window.brfv4Example.callback = this.brfv4callback
  },
  methods: {
    brfv4callback (type, status) {
      this.eventtype = type
      if (type === 'init') {
        this.countdown()
      } else if (type === 'event01') {
        if (status === 'error') {
          color = '#DC2F3E'
          this.wztip = '未通过验证，请重试'
        } else {
          color = '#6ABF8B'
          this.wztip = '请正视手机<br>左右摇头'
          window.brfv4Example.stopCheckImg = false
          this.downtime = 5
          this.countdown()
        }
      } else if (type === 'event02') {
        if (status === 'error') {

        } else {
          color = '#6ABF8B'
          this.wztip = '请正视手机<br>张开嘴'
          this.downtime = 5
          window.brfv4Example.stopCheckImg = false
          this.countdown()
        }
        // if (status === 'loading') {
        //   color = '#D6D80A'
        // } else if (status === 'error') {
        //   color = '#DC2F3E'
        // }
      } else if (type === 'event03') {
        if (status === 'error') {

        } else {
          console.log('检验通过')
        }
      }
    },
    countdown () {
      setTimeout(() => {
        this.downtime -= 1
        if (this.downtime === 0) {
          window.brfv4Example.stopCheckImg = true
        } else {
          this.countdown()
        }
      }, 1000)
    },
    drawCanvas () {
      var that = this
      var c = document.getElementById('process')
      var process = 0
      var ctx = c.getContext('2d')
      // 画灰色的圆
      function init () {
        ctx.fillStyle = '#fff'
        ctx.beginPath()
        ctx.fillRect(0, 0, that.width, that.height)
        ctx.closePath()
        ctx.beginPath()
        ctx.arc(that.width / 2, that.height / 2, that.width / 2, 0, Math.PI * 2)
        ctx.closePath()
        ctx.fillStyle = '#F6F6F6'
        ctx.fill()
      }

      function animate () {
        requestAnimationFrame(function () {
          process = process + 1
          drawCricle(ctx, process)
          if (process === 100) {
            init()
            process = 1
          }
          animate()
        })
      }

      function drawCricle (ctx, percent) {
        // 画进度环
        ctx.beginPath()
        ctx.moveTo(that.width / 2, that.height / 2)
        ctx.arc(that.width / 2, that.height / 2, that.width / 2, Math.PI * 1.5, Math.PI * (1.5 + 2 * percent / 100))
        ctx.closePath()
        ctx.fillStyle = color
        ctx.fill()

        // 画内填充圆
        ctx.beginPath()
        ctx.arc(that.width / 2, that.height / 2, that.width / 2 - 5, 0, Math.PI * 2)
        ctx.closePath()
        ctx.fillStyle = '#fff'
        ctx.fill()
      }
      animate()
    }
  }
}
</script>

<style scoped>
.wz-tip {
  margin-top: 25px;
  font-size: 20px;
  line-height: 35px;
}
.time-return {
  font-size: 38px;
  margin-top: 10px;
  font-weight: bold;
}
html, body {
  position: absolute;
  width:  100%;
  height:  100%;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  font-size: 16px;
  color: #005f9c;
  overflow: auto;
}
canvas {
  background-color: transparent;
  display: block;
  margin: auto;
  padding: 0;
}
video {
  position: absolute;
  background-color: transparent;
  display: none;
  margin: 0;
  padding: 0;
}
#_wrapper {
  position: absolute;
  width:  100%;
  height:  100%;
}
#_content {
  position: absolute;
  width: 250px;
  height: 250px;
  background-color: #f7f7f7;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
}
#_progressBar {
  position: absolute;
  width:   1px;
  height: 250px;
  background-color: #000000;
  margin: auto;
  top: 0;
  left: 0;
}
#_subline {
  position: absolute;
  width: 250px;
  height:  40px;
  background-color: transparent;
  margin: auto;
  left: 0;
  right: 0;
  top: 490px;
}
#_highlight {
  position: absolute;
  width: 250px;
  height:  auto;
  background-color: transparent;
  margin: auto;
  left: 0;
  right: 0;
  top: 525px;
  font-size: 8pt;
}
#_brfv4_logo {
  position: absolute;
  width: 200px;
  left: 0;
  bottom: 0;
  opacity: 0.5;
}
#_stats {
  position: absolute;
  width: 80px;
  height: 48px;
  left: 0;
  top: 0;
}
#_settingsLeft {
  position: absolute;
  width: 160px;
  height: 250px;
  background-color: transparent;
}
#_settingsRight {
  position: absolute;
  width: 255px;
  height:  100%;
  background-color: transparent;
  right: 0;
}
#_msg {
  position: absolute;
  width: 100%;
  height:  40px;
  background-color: transparent;
  bottom: 0;
}
.drawcanvas {
  position: absolute;
  top: 0;
  left:50%;
  margin-left: -125px;
  width: 250px;
  height: 250px;
}
.drawcanvas canvas {
  position: absolute;
  top: 0;
}
</style>



